<template>
	<view class="flex flex-direction">
		<!-- 是否登录组件 -->
		<is-login  currentPage="organ" ></is-login>
		
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">组织</block>
		</cu-custom>
		
		<!-- 受邀请加入组织 - 组件 -->
		<organ-invited></organ-invited>
		<!-- 申请企业 -->
		<organ-join></organ-join>
		
		<!-- 创建组织/分店 -->
		<view class="margin-top-sm">
			<uni-list>
				<uni-list-item showArrow to="/pages/basic/orgation/orgation_create?preview=true&currentPage=organ">
					<!-- <template slot="header">头部（图标）</template> -->
					<template slot="body">
						<view class="text-xl padding-xs text-bold">
							<text class="text-cyan">创建</text>组织/分店</view>
						<view class=" text-df padding-lr text-grey" style="line-height: 50rpx;">创建组织/分店。您将成为管理员,可邀请组织成员共同使用,协同办公。</view>
					</template>
				</uni-list-item>
			</uni-list>
		</view>
		<!-- 成为体验用户 -->
		<view class="margin-top-sm ">
			<uni-list>
				<uni-list-item >
					<template slot="body">
						<view class="text-xl padding-xs text-bold">
							<text class="text-cyan">成为体验用户</text></view>
						<view class=" text-df padding-lr text-grey" style="line-height: 50rpx;">模拟用户体验。为您提供管理员权限,预先为您导入相关数据,模拟组织/分店的工作流程。</view>
					</template>
				</uni-list-item>
				<view class="cu-form-group " style="min-height: 0rpx;"></view>
				<view class="cu-form-group">
					<view class="title padding-left-xl">组织名称</view>
					<input placeholder="请输入组织名称" name="input"></input>
				</view>
				<view class="cu-form-group ">
					<view class="title padding-left-xl">所属行业</view>
					<input placeholder="请输入/选择所属行业" name="input"></input>
				</view>
				<button class=" margin-lr margin-bottom bg-cyan cu-btn padding-lg light" @click="click_trail">立即体验</button>
			</uni-list>
		</view>
		<navigator class="margin-lr margin-top round bg-cyan cu-btn padding-lg" url=""  @tap="toSkip">
			<view >首页</view>
		</navigator>
	</view>
</template>

<script>
	/**
	 * 创建或加入组织
	 * @author 陆彦捷
	 * @date 2021.07.07 初始完成
	 * @date 2021.07.08  首页（跳过此步骤方法，改为本地缓存创建参数）
	 */
	
	var _self;
	export default {
		data() {
			return {
				// 其他参数
				preview: false,				// 是否可查看上一页
				forceIndex: false,			// 强制不判断，不跳转
			}
		},
		onLoad: function(options) {
			_self = this;
			this.getParams(options);
		},
		methods: {
			// ================================= 初始化方法 ==================================================
			/** 获取参数
			 * @param {Object} options
			 */
			getParams(options) {
				this.preview = options.preview != 'false' ? Boolean(options.preview) : false;  // 是否绑定上一页
			},
			// ================================= 监听事件 =================================================
			/** 成为体验用户
			 * @param {Object} e
			 */
			click_trail(e){
				uni.showModal({
					title:'开发中',
					content:'物初蒙稚，待养而成。请耐心等待(没有对应的接口)……'
				});
			},
			/**
			 * 跳过此步骤
			 * @param {Object} e
			 */
			toSkip : function(e){
				this.global.login.setSkipTrue();	// 设置跳过此步骤为true
				// 跳转到主页
				uni.navigateTo({
					url :'/pages/index/index'
				});
			},
			// ===================================================================================================
		}
	}
</script>

